<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <div class="card shadow-sm rounded-4 overflow-hidden mt-5">
          <div class="card-header bg-white d-flex justify-content-between align-items-center py-3">
            <h5 class="mb-0">登入驗證</h5>
          </div>
          <div class="card-body display-12 fw-normal">
            <div class="mb-3">
            <label for="add_mail" class="form-label">驗證碼</label>
            <input type="email" class="form-control" id="add_mail" v-model="verifyCode">
            </div>
            <div class="d-flex justify-content-end">
               <button class="btn btn-primary" @click="verify">驗證</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ref, getCurrentInstance, onMounted } from 'vue';
import { useRouter } from 'vue-router';

export default {
  name: 'LoginVertify',
  setup() {
    const { proxy } = getCurrentInstance();
    const account = ref('');
    const verifyCode = ref('');
    const router = useRouter();
    function verify() {
      proxy.axios.put('/v1/user/login/verify', {
        account: account.value,
        verify_code: verifyCode.value,
      })
        .then((response) => {
          if (response.data.message === '成功') {
            document.cookie = `h_MqhC49RxFRI6pR3Z_token=${response.data.access_token};`;
            router.push('/admin/users');
          }
        });
    }
    onMounted(() => {
      account.value = window.sessionStorage.getItem('H_temp_mail_store');
    });
    return {
      verifyCode,
      verify,
    };
  },
};
</script>
